.modalBackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.55);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999999; /* VERY IMPORTANT */
}

.modal {
  background: #fff;
  width: 520px;
  max-width: 90%;
  border-radius: 12px;
  padding: 20px;
  position: relative;
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.closeBtn {
  cursor: pointer;
  font-size: 18px;
}

.switchRow {
  display: flex;
  gap: 12px;
  margin: 16px 0;
}

.switchRow span {
  padding: 6px 16px;
  border-radius: 20px;
  border: 1px solid #ccc;
  cursor: pointer;
}

.switchRow .active {
  background: #000;
  color: #fff;
}

.grid2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

.input {
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  width: 100%;
}

.modalFooter {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 20px;
}

.primaryBtn {
  background: #000;
  color: #fff;
  padding: 10px 22px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
}

.outlineBtn {
  background: #fff;
  border: 1px solid #000;
  padding: 10px 22px;
  border-radius: 8px;
  cursor: pointer;
}
